<template>
    <article class="view view--project" v-bind:style="{marginLeft: margin_left}">
      <ViewHeader text="IG Helper Tool"></ViewHeader>

      <GoBackButton></GoBackButton>

      <article class="section section--project">
        <header class="header">
            IG Helper Tool to narzdzie, ktre pozwoli Ci atwiej zarzdza Twoim profilem na portalu instagram oraz zdoby wiksz ilo osb, ktre obserwuj Twj profil.
        </header>

        <section class="content">

            <ProjectSlider
                img1="../../static/img/projects/ig-helper-tool/slide1.jpg"
                img2="../../static/img/projects/ig-helper-tool/slide2.jpg"
                img3="../../static/img/projects/ig-helper-tool/slide3.jpg"
                img4="../../static/img/projects/ig-helper-tool/slide4.jpg"
                img5="../../static/img/projects/ig-helper-tool/slide5.jpg"
            >
            </ProjectSlider>

            <div class="content__video">
                <header class="header">
                    Poniej znajduje si krtki film przedstawiajcy dziaanie IG Helper Tool. Jest to film demonstracyjny przedstawiajcy dziaanie wersji 0.0.3 Szczegy na temat dziaania znajduj si ponizej.
                </header>
                
                <iframe width="100%" height="400px" src="https://www.youtube.com/embed/AYSHRQAsCcU" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
            </div>

            <div class="content__description">
                <p class="text">
                    <header class="text__header">
                        IG Helper Tool pomoze Ci w zarzdaniu swoim profilem jak i zdobywaniu nowych followersw.
                    </header>
                    

                    <hr>

                    <ul class="description-list">
                        <div class="description-list__title">
                            {{description.list.title}}
                        </div>

                        <li class="description-list__item" v-for="(item, i) in description.list.items" :key="i">
                            <span class="title">{{item.title}}</span> - <span class="description">{{item.description}}</span>
                        </li>
                    </ul>
                </p>
                
                Technologie jakie zostay uyte w projekcie to m.in: 
                <ul class="list">
                    <li class="list__item">
                        <a href="https://www.w3.org/TR/html52/" target="_blank">HTML5</a>
                        <img src="../../assets/img/icons/html5.png" alt="html5 icon" class="img icon">
                    </li>

                    <li class="list__item">
                        <a href="https://sass-lang.com/" target="_blank">SCSS</a>
                        <img src="../../assets/img/icons/sass.png" alt="scss icon" class="img icon">
                    </li>

                    <li class="list__item">
                        <a href="https://www.ecma-international.org/ecma-262/6.0/" target="_blank">ES6</a>
                        <img src="../../assets/img/icons/js.png" alt="javascript icon" class="img icon">
                    </li>

                    <li class="list__item">
                        <a href="https://developer.chrome.com/extensions" target="_blank">Chrome Extension</a>
                        <img src="../../assets/img/icons/chrome.png" alt="Chrome icon" class="img icon">
                    </li>

                    <li class="list__item">
                        <a href="https://webpack.js.org/" target="_blank">Webpack</a>
                        <img src="../../assets/img/icons/webpack.png" alt="Webpack icon" class="img icon">
                    </li>

                    <li class="list__item">
                        <a href="https://babeljs.io/" target="_blank">Babel</a>
                        <img src="../../assets/img/icons/babel.png" alt="Babel icon" class="img icon">
                    </li>

                    <li class="list__item">
                        <a href="https://vuejs.org/" target="_blank">Vue.js</a>
                        <img src="../../assets/img/icons/vue.png" alt="vue icon" class="img icon" />
                    </li>

                    <li class="list__item">
                        <a href="https://cli.vuejs.org/" target="_blank">Vue CLI3 (vuex, vue router)</a>
                        <img src="../../assets/img/icons/vue.png" alt="Vue icon" class="img icon">
                    </li>

                    <li class="list__item">
                        <a href="https://vuetifyjs.com/" target="_blank">Vuetify</a>
                        <img src="../../assets/img/icons/vuetify.png" alt="Vuetify icon" class="img icon">
                    </li>

                </ul>
            </div>

        </section>
      </article>
    </article>
</template>

<script>
import ViewHeader from '../../components/ViewHeader';
import ProjectSlider from '../../components/ProjectSlider';
import GoBackButton from '../../components/GoBackButton';

export default {
  name: 'margonemMultiBot',
  components: {ViewHeader, ProjectSlider, GoBackButton},
  data: () => {
    return {
        margin_left: '180px',
        
        description: {
            list: {
                title: "Opis funkcji:",
                items: [
                    {title: "Get Followers", description: "Klikajc Get Followers na kogo profilu otwiera list proponowanych profilw. Obserwujc osoby z tej listy zapisuj si nam one w dodatku w zakadce 'Get Followers', na podstawie listy tych profilw mozemy je automatycznie odobserwowywa."},
                    {title: "Followed Profiles", description: "Lista profilw, ktre zaobserwowalimy. Bezporednio z niej mozemy si przenie na dany profil klikajc na niego. Klikajc w ikonk kosza czycimy liste."},
                    {title: "Clear Data", description: "Czycimy ca pami aplikacji."},
                    {title: "Unfollow", description: "Automatyczne odobserwowywanie profilw z listy 'Followed Profiles'. Domyln iloi profilw do odobserwowania jest dugo listy zaobserwowanych profilw. Funkcja ta posiada losowo generowany czas oczekiwania po kazdym unfollow, czas ten bazuje na ilosci profili do odobserwowania - im jest to wiksza ilo tym bdzie duzszy czas oczekiwania, co zmniejsza prawdopodobiestwo blokady odobserwowywania. Profile, ktre zostay odobserwowane s automatycznie usuwane po akcji unfollow. Rozszerzenie wyle notyfikacj ekranow gdy zakoczy swoj prac. W tym czasie mozesz korzysta z instagrama na innej karcie przegldarki."},
                    {title: "Saved Profiles", description: "Po wejciu na profil danego uzytkownika obok jego nazwy pojawi si dodatkowy przycisk do zapisania profilu. Zapisuje on dany profil w naszym rozszerzeniu. Jego ilo followersw, osb ktre obserwuje, ilo dodanych postw oraz zdjcie profilowe. Z poziomu tej listy mozemy blokowa dane profile klikajc w rozszerzeniu przycisk 'block'. Z tej listy mozemy rwniez przej bezporednio do danego profilu."},
                    {title: "Aktualnie prace trawaj nad: ", description: ""},
                    {title: "Clear Profile", description: "funkcja pozwalajca czyci list Twoich followersw z nieaktywnych kont, spambotw oraz kont, ktre nie s atrakcyjne - Konta te to takie ktre nie przyczyniaj si do rozwoju twojego profilu. Czyli prawdopodobiestwo, ze wejd w jak interakcj z Twoim profilem jest znikome (like, komentarze, otworzenie stories, etc)"},
                ]
            }
        }
    }
  },
  mounted(){
    if (window.outerWidth <= 768){
      this.margin_left = 0;
    }
  },

  methods: {
  }
}
</script>

<style lang="scss" scoped>    
.text__header {
    padding: 0 0 1.5rem 0;
    font-size: 2rem;
    text-align: center;
}

.description-list {
    &__title {
        padding: 1.5rem 0;
        font-size: 1.7rem;
        font-weight: 600;
    }
    &__item {
        padding: .4rem .3rem .4rem 0;

        .title {
            font-weight: 600;
        }
        .description {
            line-height: 1.5;
        }
    }
}
</style>


